.catalog-countainer {
  margin: 0;
  padding: 37px 7px;
  padding-bottom: 25px;
}

@media (min-width: $tablet-width) {
  .catalog-countainer {
    width: 768px;
    margin: 0;
    padding: 67px 0;
  }
}

@media (min-width: $desktop-width) {
  .catalog-countainer {
    width: 1200px;
    margin: 0;
    padding: 100px 0;
    padding-bottom: 65px;
  }
}

.catalog-countainer__title {
  color: #000000;
  font-family: "PT Sans", sans-serif;
  font-size: 21px;
  line-height: 26px;
  font-weight: 700;
  text-transform: uppercase;
  text-align: center;
  margin: 0;
  padding: 0;
}

@media (min-width: $tablet-width) {
  .catalog-countainer__title {
    margin: 0 120px;
    padding: 0;
  }
}

@media (min-width: $desktop-width) {
  .catalog-countainer__title {
    margin: 0 200px;
    padding: 0;
  }
}

.catalog-countainer__text {
  color: #333333;
  font-family: "PT Sans", sans-serif;
  font-size: 14px;
  line-height: 21px;
  font-weight: 400;
  text-transform: uppercase;
  text-align: center;
  margin: 0;
  margin-top: 35px;
  padding: 0;
}

@media (min-width: $tablet-width) {
  .catalog-countainer__text {
    margin: 0 140px;
    margin-top: 25px;
    padding: 0;
  }
}

@media (min-width: $desktop-width) {
  .catalog-countainer__text {
    margin: 0 350px;
    margin-top: 25px;
    padding: 0;
  }
}
